<template>
  <div>
    <!-- 顶部标签 -->
    <van-nav-bar title="商城" class="bgc">
      <van-icon name="cart-o" slot="right" @click="goShopcart" />
    </van-nav-bar>

    <div class="content shopper">
      <!-- 侧边栏 -->
      <van-sidebar v-model="activeKey" @change="onChange">
        <van-sidebar-item v-for="(item,index) in typeData" :key="index" :title="item.typeDesc" />
      </van-sidebar>

      <div class="sift-box">
        <div class="sift-item" v-for="(item,index) in productData" :key="index">
          <van-row gutter="5">
            <van-col span="12">
              <van-image fit="cover" lazy-load :src="item.smallImg" />
            </van-col>
            <van-col span="12">
              <h2>{{item.name}}</h2>
              <p class="van-multi-ellipsis--l3">{{item.desc}}</p>
              <van-button @click="detailShow(item.pid)" plain custom-style>查看详情</van-button>
            </van-col>
          </van-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      // 商品种类数据
      typeData: [],

      // 商品数据
      productData: [],

      // 侧边导航栏索引值
      activeKey: 0
    };
  },

  created() {
    this.getTypeData();
  },
  computed: {
    ...mapState(["productDataCache"])
  },

  methods: {
    getTypeData() {
      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/type",
        params: {
          appkey: this.appkey
        }
      })
        .then(res => {
          this.$toast.clear();

          if (res.data.code == 400) {
            this.typeData = res.data.result;
            this.typeData.forEach((v, i) => {
              if (i == this.activeKey) {
                this.getProductData(v.type);
              }
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    getProductData(type) {
      if (this.productDataCache.length > 0) {
        this.productData = this.productDataCache;
        return;
      }

      // 加载事件
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "type",
          value: type
        }
      })
        .then(res => {
          if (res.status == 200) {
            this.$toast.clear();
            this.productData = res.data.result;

            // 缓存
            this.changeData({
              key: "productDataCache",
              value: this.productData
            });
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    // 查看详情页面跳转
    detailShow(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },

    // 跳转购物车页面
    goShopcart() {
      this.$router.push({ name: "Shopcart" });
    },

    onChange() {
      
      this.typeData.forEach((v, i) => {
        if (i == this.activeKey) {
          
          this.getProductData(v.type);
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../assets/css/commaint.less";

.shopper {
  .van-sidebar {
    position: fixed;
  }
}
.van-sidebar {
  float: left;
}
.sift-box {
  float: right;
  width: 75%;
  border-left: 1px solid #ccc;
  margin-bottom: 35px;
  .sift-item {
    height: 150px;
    padding: 5px;
    color: #130101;
    text-align: center;
    border-bottom: 1px solid #ccc;

    .van-button {
      border-radius: 10px;
      width: 100px;
    }

    p {
      text-align: left;
      margin: 5px auto;
    }

    h2 {
      margin: 6px 0;
    }
  }
}
</style>